$(document).ready(function() {  
  $('.menu-toggler').click(function (e) { 
    e.preventDefault();
    $('.navbar-menu').toggleClass('nav-mobile-show');
  });
  //index.html

  $(".member-gallery-small img").click(function(){
        $(".member-gallery-big img").prop("src",$(this).prop("src"));
  });
  
});  